*{ margin:0;
   padding:0;
   box-sizing: border-box;
   }
   .navbar{
     background-color: #fff !important;
   }
   
   .navbar-brand {
     font-size:1.9rem ! important;
     color:#565387 ! important;
   }

   .navbar a{
     font-size:1.1rem ;
     text-transform:capitalize;
   }
   .menu_active{
     font-weight:bold;
     border-bottom:1px solid #565387;
   }
   .navbar a:hover{
     color:#3498db ! important;
   }
   @media (max-width:991px){
     .navbar-brand{
     font-size: 1.5rem ! important;
   }

   .menu_active{
     font-weight:bold;
     border-bottom:none;
   }
  }
#header{
  width:100%;
  height:80vh;
  margin-top:55px;
}
#header h2{
  margin:0 0 10px 0;
  font-size:48px;
  font-weight:400;
  color:black;
}
#header .searching{
  color:#3498db;

}
#header h3{
  color:#484848;
}
#header .btn-get-started{
  font-weight:800;
  font-size:16px;
  letter-spacing:1px;
  display:inline-block;
  padding:10px 30px;
  border-radius:50px;
  transition: 0.4s;
  color:#3498db;
  border:2px solid #3498db;
  text-decoration: none;
}
#header .btn-get-started:hover{
  background:#3498db;
  color:#fff;
}
.header-img{
  text-align:right;
  /* width; */
  /* height:auto;  */
} 
.img-fluid{
   width:90%;
    height:auto;
     border-radius: 100px; 
}
.card{
  width:100%;
  display: table-cell;
}
